// src/app/control/page.tsx
"use client";

import { useEffect, useMemo, useRef, useState } from "react";
import { CheckCircle2, Info } from "lucide-react";

type Mode = "MANU" | "AUTO";

/** Les variables disponibles côté enregistrement */
type CurveKey = "FORGING_FORCE" | "Z_POSITION" | "HEAD_TEMPERATURE";

/** Un échantillon de données live (remplace la simu par tes vraies valeurs) */
type LiveSample = {
  ts: number;                 // Date.now()
  FORGING_FORCE: number;      // kN
  Z_POSITION: number;         // mm
  HEAD_TEMPERATURE: number;   // °C
};

export default function ControlPage() {
  // -------- états existants --------
  const [faultOk, setFaultOk] = useState(true);
  const [autoReady, setAutoReady] = useState(false);
  const [mode, setMode] = useState<Mode>("MANU");
  const [airCooling, setAirCooling] = useState(true);

  // -------- enregistrement --------
  const [dataRecordOn, setDataRecordOn] = useState(false);
  const [recordName, setRecordName] = useState("");
  const [selectedCurves, setSelectedCurves] = useState<Record<CurveKey, boolean>>({
    FORGING_FORCE: true,
    Z_POSITION: true,
    HEAD_TEMPERATURE: true,
  });

  // Buffer des échantillons pendant un enregistrement
  const bufferRef = useRef<LiveSample[]>([]);
  const rafRef = useRef<number | null>(null);

  // Libellés colonnes CSV
  const headers = useMemo(() => {
    const cols = ["TIMESTAMP (hh:mm:ss.ms)"];
    if (selectedCurves.FORGING_FORCE) cols.push("FORGING FORCE (kN)");
    if (selectedCurves.Z_POSITION) cols.push("Z POSITION (mm)");
    if (selectedCurves.HEAD_TEMPERATURE) cols.push("HEAD TEMPERATURE (°C)");
    return cols;
  }, [selectedCurves]);

  // Simulation de données live (remplace par ton flux PLC/WebSocket)
  function readLiveSample(prev?: LiveSample): LiveSample {
    // EXEMPLE : génère des valeurs lissées; remplace par tes vraies mesures
    const t = Date.now();
    const base = prev ?? {
      ts: t,
      FORGING_FORCE: 5,
      Z_POSITION: 0,
      HEAD_TEMPERATURE: 25,
    };
    return {
      ts: t,
      FORGING_FORCE: base.FORGING_FORCE + (Math.random() - 0.5) * 0.2,
      Z_POSITION: base.Z_POSITION + (Math.random() - 0.5) * 0.05,
      HEAD_TEMPERATURE: base.HEAD_TEMPERATURE + (Math.random() - 0.5) * 0.1,
    };
  }

  // Boucle de collecte pendant l’enregistrement (60–100 Hz via rAF ; ajuste si besoin)
  useEffect(() => {
    if (!dataRecordOn) {
      if (rafRef.current) cancelAnimationFrame(rafRef.current);
      rafRef.current = null;
      return;
    }

    let last: LiveSample | undefined;
    const tick = () => {
      const s = readLiveSample(last);
      last = s;
      bufferRef.current.push(s);
      rafRef.current = requestAnimationFrame(tick);
    };
    rafRef.current = requestAnimationFrame(tick);

    return () => {
      if (rafRef.current) cancelAnimationFrame(rafRef.current);
      rafRef.current = null;
    };
  }, [dataRecordOn]);

  // Format horodatage hh:mm:ss.ms
  function fmtTimestamp(ts: number) {
    const d = new Date(ts);
    const hh = String(d.getHours()).padStart(2, "0");
    const mm = String(d.getMinutes()).padStart(2, "0");
    const ss = String(d.getSeconds()).padStart(2, "0");
    const ms = String(d.getMilliseconds()).padStart(3, "0");
    return `${hh}:${mm}:${ss}.${ms}`;
  }

  // Export CSV (seules les colonnes cochées)
  function downloadCSV() {
    const rows: string[] = [];
    rows.push(headers.join("\t")); // tab-delimited = solide pour Excel/LibreOffice

    for (const s of bufferRef.current) {
      const line: (string | number)[] = [fmtTimestamp(s.ts)];
      if (selectedCurves.FORGING_FORCE) line.push(round(s.FORGING_FORCE, 4));
      if (selectedCurves.Z_POSITION) line.push(round(s.Z_POSITION, 4));
      if (selectedCurves.HEAD_TEMPERATURE) line.push(round(s.HEAD_TEMPERATURE, 3));
      rows.push(line.join("\t"));
    }

    const csv = "\uFEFF" + rows.join("\n"); // BOM UTF‑8
    const blob = new Blob([csv], { type: "text/csv;charset=utf-8" });
    const a = document.createElement("a");
    const name = (recordName?.trim() || "Recording") + ".csv";
    a.href = URL.createObjectURL(blob);
    a.download = name;
    a.click();
    URL.revokeObjectURL(a.href);
  }

  function round(x: number, n = 3) {
    const m = Math.pow(10, n);
    return Math.round(x * m) / m;
  }

  // Toggle record : reset buffer au démarrage, export auto à l’arrêt (optionnel)
  function toggleRecord() {
    if (!dataRecordOn) {
      bufferRef.current = [];
      setDataRecordOn(true);
    } else {
      setDataRecordOn(false);
      // Option : déclencher auto le download à l’arrêt
      downloadCSV();
    }
  }

  // UI
  return (
    <div className="min-h-screen w-full bg-[#ecf2f6]">
      <div className="mx-auto max-w-7xl px-6 py-10">
        <div className="text-2xl font-extrabold tracking-wide text-[#0f1c3a] mb-4">
          START CONDITIONS
        </div>

        <div className="bg-white rounded-2xl shadow border border-gray-200">
          <div className="p-6 md:p-8">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-8 items-center">
              <div className="space-y-6">
                {/* Fault */}
                <div className="flex items-center gap-4">
                  <button
                    onClick={() => setFaultOk(v => !v)}
                    className={`w-10 h-10 rounded-full border flex items-center justify-center
                      ${faultOk ? "bg-emerald-500 text-white border-emerald-600" : "bg-gray-200 border-gray-300 text-gray-500"}`}
                    title="Fault state"
                  >
                    <CheckCircle2 className="w-6 h-6" />
                  </button>
                  <span className="text-xl font-semibold text-[#0f1c3a]">Fault</span>
                </div>

                {/* Auto ready */}
                <div className="flex items-center gap-4">
                  <button
                    onClick={() => setAutoReady(v => !v)}
                    className={`w-10 h-10 rounded-full border flex items-center justify-center
                      ${autoReady ? "bg-emerald-400 text-white border-emerald-500" : "bg-gray-200 border-gray-300 text-gray-500"}`}
                    title="Auto readiness"
                  >
                    <Info className="w-5 h-5" />
                  </button>
                  <span className="text-xl font-semibold text-[#0f1c3a]">Auto</span>
                </div>

                <div className="text-[#0f1c3a]/80 font-semibold">
                  Need Initiales Conditions to switch Auto mode
                </div>
              </div>

              {/* Mode MANU/AUTO */}
              <div className="flex items-center justify-center gap-6">
                <button
                  onClick={() => setMode(prev => (prev === "MANU" ? "AUTO" : "MANU"))}
                  className={`w-24 h-24 rounded-full border-2 transition 
                    ${mode === "AUTO" ? "bg-emerald-500 border-emerald-600" : "bg-gray-100 border-gray-300"}`}
                  title="Toggle mode"
                />
                <div className={`text-6xl font-extrabold ${mode === "AUTO" ? "text-emerald-600" : "text-[#0f1c3a]"}`}>
                  {mode}
                </div>
              </div>
            </div>
          </div>

          {/* Bandeau Data Record */}
          <div className="bg-gray-200/60 border-t border-gray-300 p-4 md:p-6 rounded-b-2xl">
            <div className="flex flex-col gap-4">
              <div className="flex flex-col md:flex-row md:items-center gap-4">
                <button
                  onClick={toggleRecord}
                  className={`px-4 py-2 rounded-full font-semibold shadow 
                    ${dataRecordOn ? "bg-sky-600 text-white" : "bg-gray-300 text-gray-700"}`}
                >
                  {dataRecordOn ? "Stop & Save CSV" : "Start Record"}
                </button>

                {!dataRecordOn && (
                  <button
                    onClick={downloadCSV}
                    className="px-4 py-2 rounded-full font-semibold shadow bg-white border border-gray-300 text-[#0f1c3a]"
                    title="Download current buffer as CSV"
                  >
                    Download CSV
                  </button>
                )}

                <div className="flex-1">
                  <label className="block text-sm font-semibold text-[#0f1c3a] mb-1">
                    Write the name of this recording file here
                  </label>
                  <input
                    className="w-full bg-white border border-gray-300 rounded-lg px-3 py-2 outline-none focus:ring-2 focus:ring-sky-400"
                    value={recordName}
                    onChange={(e) => setRecordName(e.target.value)}
                    placeholder="MyRecording_001"
                  />
                </div>
              </div>

              {/* Sélection des courbes */}
              <div className="grid grid-cols-1 sm:grid-cols-3 gap-3">
                <CurveCheckbox
                  label="FORGING FORCE (kN)"
                  checked={selectedCurves.FORGING_FORCE}
                  onChange={(v) => setSelectedCurves(s => ({ ...s, FORGING_FORCE: v }))}
                />
                <CurveCheckbox
                  label="Z POSITION (mm)"
                  checked={selectedCurves.Z_POSITION}
                  onChange={(v) => setSelectedCurves(s => ({ ...s, Z_POSITION: v }))}
                />
                <CurveCheckbox
                  label="HEAD TEMPERATURE (°C)"
                  checked={selectedCurves.HEAD_TEMPERATURE}
                  onChange={(v) => setSelectedCurves(s => ({ ...s, HEAD_TEMPERATURE: v }))}
                />
              </div>

              {/* Indication taille buffer */}
              <div className="text-sm text-gray-600">
                Samples buffered: <span className="font-semibold">{bufferRef.current.length}</span>
              </div>
            </div>
          </div>
        </div>

        {/* Footer action */}
        <div className="mt-6 flex items-center justify-end gap-3">
          <button
            onClick={() => setAirCooling(v => !v)}
            className={`flex items-center gap-3 px-5 py-3 rounded-full font-semibold shadow
              ${airCooling ? "bg-[#0f1c3a] text-white" : "bg-gray-300 text-gray-700"}`}
          >
            <CheckCircle2 className="w-5 h-5" />
            Air Cooling
          </button>
        </div>
      </div>
    </div>
  );
}

function CurveCheckbox({
  label,
  checked,
  onChange,
}: {
  label: string;
  checked: boolean;
  onChange: (v: boolean) => void;
}) {
  return (
    <label className="flex items-center gap-3 bg-white rounded-xl border border-gray-200 px-4 py-3 shadow-sm">
      <input
        type="checkbox"
        className="w-5 h-5"
        checked={checked}
        onChange={(e) => onChange(e.target.checked)}
      />
      <span className="text-[#0f1c3a] font-medium">{label}</span>
    </label>
  );
}
